.portfolio__card {
	display: flex;
	flex-direction: column;
	position: relative;
	width: 250px;
	height: 300px;
	font-family: var(--font-family);
	background: var(--color-blog);
	align-items: center;
	justify-content: center;
	overflow: hidden;
	margin: 0 1rem;
	border-radius: 24px;
	transition: all .7s ease-in-out;
	text-align: center;
}

.portfolio__card-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
}

.portfolio__card-container h1 {
	color: var(--color-text);
	font-size: 1rem;
	font-weight: 600;
}

.portfolio__card-row {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

.portfolio__card-row img {
	filter: invert(.75);
}

.portfolio__card-row img:hover {
	filter: invert(1);
	scale: 1.1;
	transition: all .1s ease-in-out;
}

.portfolio__card-frame img {
	width: 100%;
	height: 200px;
	object-fit: cover;
	pointer-events: none;
}

.portfolio__card:hover {
	transform: scale(1.05);
}

.portfolio__card:hover .portfolio__card-frame img {
	filter: saturate(1.1);
}

.portfolio__card h1{
	color: var(--color-text);
	font-size: 1.2rem;
	font-weight: 600;
}

.portoflio__card-modal {
	position: relative;
	background: var(--color-blog);
	transition: all .7s ease-in-out;
	visibility: hidden;
	outline: none;
}

.portfolio__card-modal-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.portfolio__card-modal-container h1 {
	color: var(--color-text);
	font-size: 2rem;
	font-weight: 600;
	font-family: var(--font-family);
	padding: 0;
}

.portfolio__card-modal-frame {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.portfolio__card-modal-frame img {
	max-width: 750px;
	object-fit: cover;
}

.portfolio__card-modal-container-body {
	display: flex;
	max-width: 750px;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.portfolio__card-modal-container-body p {
	font-size: 1rem;
	font-weight: 400;
	font-family: var(--font-family);
	color: var(--color-text);
	text-align: center;
}

.portfolio__card-modal-container-grid {
	display: grid;
	max-width: 500px;
	grid-template-columns: 1fr;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	margin: auto;
	padding: 2rem 0;
}

.portfolio__card-modal-container-grid img {
	width: 100%;
	object-fit: cover;
}

@media screen and (max-width: 1050px) {
	.portfolio__card-modal-container {
		max-width: 600px;
	}
	.portfolio__card-modal-container-body {
		max-width: 600px;
	}
	.portfolio__card-modal-container-body p {
		max-width: 600px;
	}
}

@media screen and (max-width: 700px) {
	.portfolio__card-modal-container-body {
		max-width: 400px;
	}
	.portfolio__card-modal-container-body p {
		max-width: 400px;
	}
}

@media screen and (max-width: 550px) {
	.portfolio__card-modal-container-body {
		max-width: 300px;
	}
	.portfolio__card-modal-container-body p {
		max-width: 300px;
	}
}
